@import url('https://fonts.googleapis.com/css2?family=Great+Vibes&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.fancy-font {
    font-family: 'Great Vibes', cursive;
    padding: 1rem;
}

.navbar {
    list-style-type: none;
    background-color: rgb(226, 219, 209);
    display: flex;
    justify-content: space-around;

    .nav-link {
        display: inline-block;
        padding: 1rem 2rem;
        text-decoration: none;
        color: black;

        &:hover {
            background-color: rgb(194, 204, 204);
        }
    }
}

main {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1rem;
    margin: 2rem 4rem;
    min-height: 100vh;

    .grid-item {
        aspect-ratio: 1;
        background-image: url('https://cdn.pixabay.com/photo/2017/06/02/18/24/watermelon-2367029_1280.jpg');
        background-size: cover;
        background-position: center;
        position: relative;

        p {
            color: whitesmoke;
            background-color: rgba(0,0,0,0.5);
            position: absolute;
            bottom: 0;
            width: 100%;
            text-align: center;
            padding: 1rem 0;
        }
    }

    #one {
        background-image: url('https://cdn.pixabay.com/photo/2017/09/16/19/21/salad-2756467_640.jpg');
    }
}

footer {
    background-color: rgb(32, 32, 32);
    color: whitesmoke;
    padding: 2rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1rem;

    p {
        text-align: center;
    }

    ul {
        // margin: 0 auto;
        width: 60%;
        list-style-type: none;
        display: flex;
        justify-content: space-evenly;
    }
}


@media screen and (width < 600px) {
    main {
        grid-template-columns: 1fr;
    }
}

@media screen and (min-width: 600px) and (max-width: 1200px) {
    main {
        grid-template-columns: 1fr 1fr;
    }
}